<template>
    <div id="aboutTechnology">
      <!--轮播图-->
      <div id="bannerImg">
        <img src="../../static/img/case1.png" alt="雅洁五金"/>
      </div>
      <!--选择-->
      <div id="select">
        <div class="selectBtn" v-for="(item,index) in fiveBtn" :class="{'selectBtnHas':nowIndex==index}">
          <router-link :to="item.to"><span>{{item.name}}</span></router-link>
        </div>
      </div>
      <!--主要内容区-->
      <div id="main">
        <div class="main2" v-for="(item,index) in videoData" :key="index">
          <div class="mainVideo">
            <div class="videoBox">
              <iframe  :src='item.path' width="350" height="260" frameborder=0 allowfullscreen="true" allowtransparency="true"></iframe>
            </div>
            <div class="bodyBox">
              <div class="videoText">
                <ul>
                  <li>
                    <p>{{item.title}}</p>
                  </li>
                  <li>
                    <p>{{item.desc}}</p>
                  </li>
                </ul>
                <!--<div class="icon"><img src="../../static/img/index5.png" alt=""></div>-->
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
      name: "aboutTechnology",
      data(){
        return {
          nowIndex:4,
          fiveBtn:[
            {
              to:'/aboutBrief',
              name:'公司简介'
            },
            {
              to:'/aboutProgress',
              name:'发展历程'
            },
            {
              to:'/aboutHonor',
              name:'品牌荣誉'
            },
            {
              to:'/aboutCase',
              name:'工程案例'
            },
            {
              to:'/aboutTechnology',
              name:'创新科技'
            },
            {
              to:'/aboutContactUs',
              name:'联系我们'
            }
          ],
          videoData:[]
        }
      },
      mounted(){
        const _this = this;
        // 获取创新技术
        this.$api.axiosGet('/index/index/getTechnology/location/1',{},function (data) {
          // console.log(data);
          _this.videoData = data.data.technology;
        });
      }
    }
</script>

<style scoped>
@import "../../static/css/about_technology.css";
</style>
<style>
  @import "../../static/css/public.css";
</style>



